<template>
    <div id="takeout-delivery">
        <public-header title="配送设置"></public-header>
        <div class="content">
            <van-radio-group v-model="order.dispatch_sort">
                <van-cell-group>
                    <van-cell title="订单调度页配送员排序方式"></van-cell>
                    <van-cell title="配送员编号倒序" clickable @click="order.dispatch_sort = 'deliveryer_id'" class="border-0px">
                        <van-radio name="deliveryer_id" />
                    </van-cell>
                    <van-cell title="配送员距离门店的距离排序" clickable @click="order.dispatch_sort = 'store2deliveryer_distance'" class="border-0px">
                        <van-radio name="store2deliveryer_distance" />
                    </van-cell>
                </van-cell-group>
            </van-radio-group>
            <van-cell-group  class="margin-10-t">
                <van-cell>
                    <div slot="title">待抢订单列表显示收货地址</div>
                    <iswitch :value="order.show_acceptaddress_when_firstdelivery" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="order.show_acceptaddress_when_firstdelivery"></iswitch>
                </van-cell>
                <van-cell>
                    <div slot="title">带抢订单最多显示</div>
                    <div slot="right-icon">
                        <div class="input-unit">
                            <input type="number" v-model="order.max_dispatching"><span>单</span>
                        </div>
                    </div>
                </van-cell>
            </van-cell-group>
            <van-cell-group class="margin-10-t">
                <van-cell>
                    <div slot="title">配送员外卖单开启自动刷新</div>
                    <iswitch :value="order.auto_refresh" condition-open="1" condition-close="2" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="order.auto_refresh" ></iswitch>
                </van-cell>
            </van-cell-group>
            <van-radio-group v-model="order.dispatch_mode" class="margin-10-t">
                <van-cell-group>
                    <van-cell title="外卖单派单模式"></van-cell>
                    <van-cell title="抢单模式" clickable @click="order.dispatch_mode = 1" class="border-0px">
                        <van-radio :name="1" />
                    </van-cell>
                    <van-cell title="管理员派单" clickable @click="order.dispatch_mode = 2" class="border-0px">
                        <van-radio :name="2" />
                    </van-cell>
                    <van-cell title="系统分配" clickable @click="order.dispatch_mode = 3" class="border-0px">
                        <van-radio :name="3" />
                    </van-cell>
                </van-cell-group>
            </van-radio-group>
            <van-cell-group class="margin-10-t">
                <van-cell>
                    <div slot="title">管理员派单/系统分配模式允许抢单</div>
                    <iswitch :value="order.can_collect_order" condition-open="1" condition-close="0" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="order.can_collect_order"></iswitch>
                </van-cell>
            </van-cell-group>
            <div class="padding-15">
                <van-button size="normal" class="bg-primary font-16" block @click="onSubmit">提交</van-button>
            </div>
        </div>
        <iloading v-if="showPreLoading"></iloading>
    </div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				order: {
					dispatch_sort: 'deliveryer_id',
					show_acceptaddress_when_firstdelivery: '1',
					max_dispatching: '0',
					auto_refresh: '1',
					dispatch_mode: '1',
					can_collect_order: '1',
                }
			}
		},
		components: {
			publicHeader,
			iswitch
		},
		methods: {
			onLoad(){
				var that = this;
				getData({
					vue: this,
					autoAssign: true,
					variable: 'order',
					url: 'plateform/agent/config/order'
				});
			},

			jsToggleSwitch(res){
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
			},

			onSubmit(){
				var that = this;
				setData({
					vue: this,
					url: 'plateform/agent/config/order',
					data: that.order
				});
            }
		},
        mounted(){
			this.onLoad();
        }
	}
</script>

<style>

</style>


